import { useEffect, useState } from "react";
import { bitcoinService } from "../../services/bitcoinService.js";
import { useDispatch, useSelector } from "react-redux";
import { setLoggedInUser } from "../../store/actions/userActions.js";
import { loadTransfers } from "../../store/actions/transferActions.js";
import { TransfersTable } from "../../cmps/TransfersTable/TransfersTable.jsx";
import { RegisterBtn } from "../../cmps/RegisterBtn/RegisterBtn.jsx";
import NiceButton from "../../cmps/NiceButton/NiceButton.jsx";
import plusLogo from "../../assets/icons/plus-black.png";
import minusLogo from "../../assets/icons/minus.png";

export const HomePage = ({ history }) => {
  const [rateByDollar, setBitcoinRate] = useState(0);
  const [isAllTransfers, setIsAllTransfers] = useState(false);
  const [transfersToShow, setTransfersToShow] = useState(null);
  const loggedUser = useSelector((state) => state.userReducer.user);
  const transfers = useSelector((state) => state.transferReducer.transfers);

  const dispatch = useDispatch();
  const rateByBitcoin = Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(1 / rateByDollar);

  const totalValue =
    !loggedUser ||
    Intl.NumberFormat("en-US", {
      style: "currency",
      currency: "USD",
      minimumFractionDigits: 0,
      maximumFractionDigits: 0,
    }).format((1 / rateByDollar) * loggedUser.coins);

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser());
  }, [loggedUser, dispatch]);

  useEffect(() => {
    if (loggedUser && !transfersToShow) {
      dispatch(loadTransfers());
    }
  }, [dispatch, loggedUser, transfersToShow]);

  useEffect(() => {
    const loadBitcoinRate = async () => {
      const fetchRate = await bitcoinService.getRate();
      setBitcoinRate(fetchRate);
    };
    if (loggedUser && !rateByDollar) loadBitcoinRate();
  }, [rateByDollar, loggedUser]);

  useEffect(() => {
    if (loggedUser) {
      if (transfers) {
        if (isAllTransfers) setTransfersToShow(transfers);
        else setTransfersToShow(transfers?.slice(0, 3));
      }
    }
  }, [isAllTransfers, transfers, loggedUser]);

  const iaMoreLogo = isAllTransfers ? minusLogo : plusLogo;
  const tableTitle = isAllTransfers ? "All Transfers" : "Last 3 Transfers";
  const amountClass = loggedUser?.coins ? "amount-plus" : "amount-minus";

  const IconMoreLessBtn = () => <img src={iaMoreLogo} alt="" />;

  return loggedUser ? (
    <div className="home-page logged-user">
      <div className="user-balance">
        <h4>Wallet Balance:</h4>
        <h2 className={amountClass}>{totalValue}</h2>
        <h3>
          You have{" "}
          <span className={amountClass}>{loggedUser.coins.toFixed(2)}</span>{" "}
          bitcoins
        </h3>
        <h4>1 Bitcoin: {rateByBitcoin}</h4>
      </div>
      {transfersToShow && (
        <div className="transfers-table">
          {transfers.length > 3 && (
            <NiceButton
              onClick={() => setIsAllTransfers((prev) => !prev)}
              Icon={IconMoreLessBtn}
              className={"more-less-btn"}
            ></NiceButton>
          )}
          <TransfersTable title={tableTitle} rows={transfersToShow} />
        </div>
      )}
    </div>
  ) : (
    <div className="home-page guest">
      <div className="guest-welcome">
        <h1>Welcome to Mister Bitcoin</h1>
        <h3>Demo bitcoin wallet</h3>
        <RegisterBtn />
        <p onClick={() => history.push("/signup/login")}>
          Already registered? login
        </p>
      </div>
    </div>
  );
};
