import { transferService } from "../../services/transferService";

export const loadTransfers = () => {
  return async (dispatch) => {
    const transfers = await transferService.getTransfers();
    dispatch({ type: "SET_TRANSFERS", transfers });
  };
};

export const sendCoins = (contactEmail, transferAmount) => {
  return async (dispatch) => {
    const { newTransfer, updatedUser } = await transferService.addTransfer(
      contactEmail,
      transferAmount
    );
    dispatch({ type: "ADD_TRANSFER", transfer: newTransfer });
    dispatch({ type: "SET_USER", user: updatedUser });
  };
};
