const INITIAL_STATE = {
  transfers: null,
};

export function transferReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "SET_TRANSFERS":
      return {
        ...state,
        transfers: action.transfers,
      };
    case "ADD_TRANSFER":
      return {
        ...state,
        transfers: [...state.transfers, action.transfer],
      };
    default:
      return state;
  }
}
