import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { useSelector } from "react-redux";
import { Typography } from "@mui/material";

export const TransfersTable = ({ rows, title }) => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  return (
    <TableContainer component={Paper}>
      <Typography variant="h4">{title}</Typography>
      <Table sx={{ minWidth: 650 }} aria-label="sticky table">
        <TableHead>
          <TableRow hover role="checkbox">
            <TableCell>Date</TableCell>
            <TableCell>From</TableCell>
            <TableCell>To</TableCell>
            <TableCell>Amount</TableCell>
            <TableCell>Balance</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              hover={true}
              key={row._id}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell>
                {new Date(row.createdAt).toLocaleDateString()}
              </TableCell>
              {row.from === loggedUser.email ? (
                <TableCell>{loggedUser.fullname}</TableCell>
              ) : (
                <TableCell>{row.from}</TableCell>
              )}
              {row.to === loggedUser.email ? (
                <TableCell>{loggedUser.fullname}</TableCell>
              ) : (
                <TableCell>{row.to}</TableCell>
              )}
              <TableCell
                className={
                  loggedUser.email === row.to ? "amount-plus" : "amount-minus"
                }
              >
                {loggedUser.email === row.to
                  ? `+${row.transferAmount}`
                  : `-${row.transferAmount}`}
              </TableCell>
              <TableCell>{row.updatedUserAmount.toFixed(2)}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};
