import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { TransferFund } from "../../cmps/TransferFund/TransferFund";
import { setLoggedInUser } from "../../store/actions/userActions";
import { loadTransfers, sendCoins } from "../../store/actions/transferActions";
import altContactImg from "../../assets/icons/contact.png";

import { TransfersTable } from "../../cmps/TransfersTable/TransfersTable";
import NiceButton from "../../cmps/NiceButton/NiceButton";
import editBtnLogo from "../../assets/icons/edit.png";
import plusLogo from "../../assets/icons/plus-black.png";
import minusLogo from "../../assets/icons/minus.png";
import loadingGif from "../../assets/icons/loading.gif";

export const ContactDetailsPage = (props) => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const transfers = useSelector((state) => state.transferReducer.transfers);
  const [contact, setContact] = useState(null);
  const [transfersToShow, setTransfersToShow] = useState(null);
  const [userContactTransfers, setUserContactTransfers] = useState(null);
  const [isAllTransfers, setIsAllTransfers] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const dispatch = useDispatch();

  const iaMoreLogo = isAllTransfers ? minusLogo : plusLogo;

  useEffect(() => {
    if (!loggedUser) dispatch(setLoggedInUser());
    else dispatch(loadTransfers());
  }, [loggedUser, dispatch]);

  useEffect(() => {
    if (loggedUser) {
      const id = props.match.params.id;
      const contact = loggedUser.contacts.find((contact) => contact._id === id);
      setContact(contact);
    }
  }, [loggedUser, props.match.params.id]);

  useEffect(() => {
    if (contact && transfers) {
      const transferByContact = transfers.filter(
        (transfer) =>
          contact.contactEmail === transfer.to ||
          contact.contactEmail === transfer.from
      );
      if (transferByContact) {
        setUserContactTransfers(transferByContact);
        if (isAllTransfers) setTransfersToShow(transferByContact);
        else setTransfersToShow(transferByContact.slice(0, 3));
      }
    }
  }, [isAllTransfers, transfers, contact]);

  const onEditContact = () => {
    props.history.push(`/contact/edit/${contact._id}`);
  };

  const onTransferCoins = (transferAmount, ev) => {
    setIsLoading(true);
    ev.preventDefault();
    if (loggedUser.coins < transferAmount) return alert("No enough money");
    dispatch(sendCoins(contact.contactEmail, transferAmount)).then(() => {
      setIsLoading(false);
      props.history.push("/contact");
    });
  };

  const IconMoreLessBtn = () => <img src={iaMoreLogo} alt="" />;

  const IconEdit = () => <img src={editBtnLogo} alt="" />;

  return contact ? (
    <div className="contact-details-page">
      <div className="contact-details">
        <NiceButton
          Icon={IconEdit}
          onClick={onEditContact}
          className="edit-logo"
        />
        <div className="contact-img-details">
          {contact.contactImg ? (
            <img src={contact.contactImg} alt="" className="contact-img" />
          ) : (
            <img src={altContactImg} alt="" className="contact-img" />
          )}
        </div>
        <p>Name: {contact.contactName}</p>
        <p>Email: {contact.contactEmail}</p>
        <p>Phone: {contact.contactPhone}</p>
      </div>
      {isLoading ? (
        <img className="loading-transfer" src={loadingGif} alt="" />
      ) : (
        <TransferFund contact={contact} onTransferCoins={onTransferCoins} />
      )}
      {userContactTransfers && (
        <div className="transfers-by-contact">
          {userContactTransfers.length > 3 && (
            <NiceButton
              onClick={() => setIsAllTransfers((prev) => !prev)}
              Icon={IconMoreLessBtn}
              className={"more-less-btn"}
            ></NiceButton>
          )}
          <TransfersTable
            title="Transfers with contact"
            rows={transfersToShow}
          />
        </div>
      )}
    </div>
  ) : (
    <div>Contact not found...</div>
  );
};
